.product-add-form {
    margin: 30px 0px;
    padding: 25px;
    background-color: #1167b1;
    border-radius: 0.375rem;
    color: #fff;  
}


.add-form {
    margin-top: 20px;
}

.add-form input {
    width: 350px;
    margin-right: 20px;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.375rem;
}

.btn {
    margin-top: 20px;
    padding: 10px 24px;
    border: 2px solid #fff;
    background-color: #1167b1;
    color: whitesmoke;
    border-radius: 0.375rem;
    font-size: 1rem;
    cursor: pointer;
    transition-duration: 0.3s;
    display: inline-block;
    text-align: center;
    min-width: 150px;
}

.btn:hover {
    background-color: whitesmoke;
    color: #000;
}