.product-list-group-header {
    padding: 15px 20px;
    /* text-align: center; */
}

.product-list-group-header span {
    
    margin: 0 50px;
    line-height: 35px;
    font-size: 20px;
    cursor: pointer;
    min-width: 550px;
    padding: 0 20px 0 20px;
}

.product-list-group-header i {
    /* margin: 0 50px; */
    line-height: 35px;
    font-size: 20px;
}

.product-list-group-header button {
    /* margin: 0 50px; */
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}