.product-list-group-item {
    padding: 15px 20px;
    border-bottom: 1px solid #3d5a80;
    display: flex;
    text-align: center;
}

.inner-container {
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
}

.product-list-group-item:last-child {
    border-bottom: none;
}

.product-label {
    /* margin: 0 50px; */
    line-height: 35px;
    font-size: 20px;
    cursor: pointer;
    min-width: 150px;
    align-self: flex-start;
}

.product-list-group-item button {
    width: 35px;
    height: 35px;
    margin: 0px 15px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}

.product-list-group-item .fa-heart {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color: #0d00ff;
    transition: 0.3s all;
    transform: translateX(30px);
    opacity: 0;
}

.product-list-group-item.like .fa-heart {
    opacity: 1;
    transform: translateX(30px);
}

.product-list-group-item.run-out .product-list-group-label,
.product-list-group-item.run-out .product-list-header-pcs,
.product-list-group-item.run-out .product-list-header-price {
    color: #cc5947;
}

.product-list-group-item.run-out .fa-hourglass-end {
    color: #cc5947;
}


.product-list-group-item.buy .fa-cart-shopping {
    color: green;
}

[data-tooltip] {
    position: relative;
}

[data-tooltip]::after {
    font-size: 12px;
    padding: 5px;
    content: attr(data-tooltip);
    position: absolute;
    left:0;
    top:0;
    background-color: #1167b1;
    border-radius: 5px;
    color: #fff;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    pointer-events: none;
    opacity: 0;
    transition: 0.3s;
}

[data-tooltip]:hover::after {
    opacity: 0.7;
    top: 3em;
    left: 2em;
}






